.Navbar_container {
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100vw !important;
    background-color: orange !important;
}

.navbar_containt {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 0;
    left: 0rem;
    right: 0rem;
    z-index: 99999999999;
    background-color: orange;
    padding: 0rem 2rem;
}


/* logo_section */
.logo_section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

.logo_section .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    text-decoration: none;
}


.logo_section img {
   width: 4.5rem;
}

@font-face {
    font-family: ultra-regulare;
    src: url(../Font-familly/Flamenco-Regular.ttf);
}

.logo_section h3 {
    font-size: 1.4792rem;
    font-family: ultra-regulare;
    margin: 0;
    color: rgb(110, 47, 47);
}

.logo_section .menu_icon {
    display: none;
}


/* link_section */
.link_section ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.link_section ul li {
    list-style-type: none;
}

.link_section ul li a {
    text-decoration: none;
    font-weight: bold;
    color: rgb(110, 47, 47);
    white-space: nowrap;
}

 .bi-cart-dash {
    font-size: 1.347rem;
    color: brown;
    border-radius: 9rem;
    padding: .2rem .5rem;
    cursor: pointer;
    background-color: rgb(241, 205, 151);
}

.bi-heart-fill.navBar {
    font-size: 1.5rem;
}

.bi-heart-fill.mobile {
    font-size: 1.5rem !important;
}





/* Media Query */
@media (max-width: 800px) {
    .link_section ul {
        display: none;
    }

    .link_section ul li i {
        display: none;
    }

    .logo_section {
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .logo_section .logo {
        justify-content: start;
        width: 100%;
    }


    .logo_section .menu_icon {
        display: flex;
        gap: 1rem;
        justify-content: center;
        align-items: center;
    }

    .logo_section .menu_icon i {
        cursor: pointer;
        font-size: 2rem;
        transition: .3s;
        color: rgb(109, 36, 36);
     }

     .logo_section .menu_icon .bi-cart-dash {
        font-size: 1.4rem;
     }

     .logo_section .menu_icon i:hover {
        color: red;
     }

     /* /////////////////////////////////////////////////////////// */
     .Navbar_container.active .link_section ul {
        display: flex;
        align-items: center;
        margin-top: 3rem;
        margin-bottom: 3rem;
    }

    .Navbar_container.active .navbar_containt {
        flex-direction: column;
        background-color: orange;
        left: 0;
        right: 0;
        padding: 0 1rem;
    }

    .Navbar_container.active .navbar_containt ul {
        flex-direction: column;
        padding-left: 0;
    }

    .cart_notif {
        left: -.40rem !important;
        top: -.5rem !important;
        display: none;
    }

    .fav_notif {
        left: -.80rem !important;
        top: -.4rem !important;
        
    }

    .Navbar_container .navbar_containt ul li div {
        display: none !important;
    }


}

@media (max-width: 600px) {
    .logo_section h3 {
        font-size: 1.142rem;

    }

    .logo_section img {
        width: 3rem;
     }




}

@media (max-width: 400px) {

    .navbar_containt {
    padding: 0rem 1rem;
}


    .logo_section h3 {
        font-size: .81rem;
        transform: translateY(.1rem);

    }

    .logo_section img {
        width: 2rem;
     }



     

     
}





.fav_notif_box {
  position: relative;
}

.fav_notif, .cart_notif {
    position: absolute;
    color: brown;
    width: 1.3rem;
    height: 1.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5rem;
    background-color: rgba(255, 255, 255, 0.521);
    top: -.81rem;
    left: -.81rem;
    font-size: .8rem;
}

.cart_notif_box {
    position: relative;
}

.cart_notif {
    left: -.6rem;
}














