.Offer_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-bottom: 6rem;
    
}

.Offer_container .Offer_containt {
    width: 90%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 4rem;
}


/* title section */
.Offer_container .title_section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 0rem !important;
}

.Offer_container .title_section h2 {
    font-size: 3rem;
    margin: 0;
    color: rgb(80, 37, 37);
}

.Offer_container .title_section p {
    font-size: 1rem;
    margin: .5rem;
    color: brown;
}



/* burger_section */
.Offer_container .burger_section {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 3rem;
}

.Offer_container .burger_section .burger_1 {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    box-shadow: 0 0 2rem rgba(146, 146, 146, 0.3);
    box-shadow: 0 0 2rem rgba(146, 146, 146, 0.374);
    border-radius: 1rem;

}

.Offer_container .burger_section .image_box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.Offer_container .burger_section .image_box .burger {
    width: 100%;
    height: 17rem;
    object-fit: cover;
    border-radius: .71rem;
}

.Offer_container .burger_2 {
    width: 100% !important;
}

.Offer_container .burger_section .shadow {
    position: absolute;
    bottom: -1.252rem;
    width: 70%;
}


/* description */
.Offer_container .burger_section .title_box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    margin-top: .51rem;
}


.Offer_container .burger_section .title_box h3 {
    font-size: 1.5rem;
    margin: 0;
    color: rgb(77, 33, 33);
}

.Offer_container .burger_section .title_box p {
    color: brown;
    font-weight: bold;
    font-size: 1.6rem;
    margin-top: 0;
    margin-bottom: 1rem;
}

.Offer_container .burger_section .title_box i {
    font-size: 1.5rem;
    margin: 0 .2rem;
    color: brown;
}

.Offer_container .burger_section .button_section {
    width: 100%;
}

.Offer_container .burger_section .title_box button {
    width: 14rem;
    width: 100%;
    height: 2.3rem;
    background-color: rgba(255, 166, 0, 0.205);
    color: brown;
    font-weight: bold;
    font-size: 1.1rem;
    border: 1px solid brown;
    cursor: pointer;
    transition: .25s;
    border-radius: .4rem;

}

.Offer_container .burger_section .title_box button:hover {
   /* width: 12rem; */
   border-radius: 1rem;

}



/* Media Query */
@media (max-width: 900px) {
    .Offer_container .burger_section {
       grid-template-columns: 1fr 1fr;
    }

   
}

@media (max-width: 700px) {
    .Offer_container .burger_section {
      gap: 1rem;
    }

}

@media (max-width: 600px) {
    .Offer_container .burger_section {
        grid-template-columns: 1fr;
    }

    .Offer_container .title_section h2 {
        font-size: clamp(.51rem, 7vw, 7rem);
    }

    .Offer_container .title_section p {
        font-size: .781rem;
        text-align: center;
    }


}

















