.Pupup_container {
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 99999999999999999;
    background-color: rgba(0, 0, 0, 0.514);
}

.pupup_containt {
    width: 30rem;
    height: 30rem;
    background-color: white;
    border: 1px solid gray;

}

.pupup_containt img {
    width: 20rem;
    height: 15rem;
    object-fit: cover;
    border-radius: 1rem;
}

.pupup_containt h3 {
    color: green;
}

.pupup_containt .name {
    font-size: 1.4rem;
    font-weight: bold;
    margin: 0;
}

.pupup_containt .price {
    font-size: 1.2rem;
    font-weight: bold;
    color: brown;
    margin: .5rem;
}

.pupup_containt .buttons {
    display: flex;
    gap: 1rem;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.pupup_containt .buttons button {
    padding: .45rem 3rem;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: .25s;
}

.pupup_containt .buttons button:hover {
    border-radius: 10rem;
}

.pupup_containt .buttons .back {
    background-color: orange;
    color: brown;
}

.pupup_containt .buttons .cart {
    background-color: white;
    border: 1px solid brown;
    color: brown;
}


@media (max-width: 501px) {
    .pupup_containt {
        width: 80%;
        height: 80vh;
    
    }

    .pupup_containt img {
        width: 70vw;
        height: 15rem;
    }

    .pupup_btn {
        width: 40%;
        border: 1px solid brown;
    } 

    .pupup_containt .buttons button{
        padding: .45rem 0rem;
        width: 40% ;
        border: none !important;
    }


}