/* General style */
.Hero_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(to left, orange, rgb(245, 183, 67));
    overflow-x: hidden;
}

.hero_containt {
    display: flex;
    justify-content: space-around;
    width: 92%;
    
}








/* ////////////////////////////////////////////////////////////////////// */
/* ////////////////////////////////////////////////////////////////////// */
/* Left part style */
.left_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
}

@font-face {
    font-family: Moul-Regular;
    src: url(../../Font-familly/Moul-Regular.ttf);
}

.hero_containt h1, h4 {
     font-family: Moul-Regular;
    font-size: 8vw;
    font-size: clamp(4rem, 8vw, 7rem);
    margin: 0;
    color: rgb(66, 30, 30);
}

.hero_containt h4 {
    font-size: clamp(2rem, 4vw, 4rem);


}


.left_section p {
    text-transform: uppercase;
    font-size: 20px;
    margin: 0rem;
    padding: .5rem;
    border: 1px dashed rgb(66, 30, 30);
    color: rgb(66, 30, 30);
    margin-bottom: 1.52rem;

    
}


/* ////////////////////////////////////////////////////////////////////// */
/* ////////////////////////////////////////////////////////////////////// */
/* Right part style */
.right_section {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
}


.image_container {
    background-image: radial-gradient(white 5%,rgb(255, 166, 0),  rgb(245, 183, 67));
    border-radius: 100rem;
}

.Hero_container img {
    transform: rotateY(180deg);
    width: 100%;
}


/* Pricing container */
/* ////////////////////////////////////////////////////////////////////////////////////////// */

.price_container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0;
    left: 2rem;
    border-radius: 10rem;
    width: 12vw;
    height: 12vw;
    background-color: rgb(255, 25, 25);
    color: white ;
}

.price_containt {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 2px dashed white;
    width: 10vw;
    height: 10vw;
    border-radius: 10rem;
}

.price_container h2 {
    margin: 0;
    font-size: 3.8vw;
}

.price_container span {
    font-size: 1.8vw;
}

.price_container h3 {
    margin: 0;
    font-size: 1.8vw;
    letter-spacing: .35rem;
}



/* shadow */
/* ///////////////////////////////////////////////////////////////////////// */
.shadow1 {
    position: absolute;
    bottom: -.5rem;
    right: 10.9vw;
    width: 50%;
    opacity: 90%;
}

.shadow2 {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    width: 30%;
    opacity: 50%;
}


/* Media Quiery */
@media (max-width: 700px) {
    .hero_containt {
        flex-direction: column-reverse;
        align-items: center;
        gap: 3rem;
        margin-top: 5rem;
    }

    /* left_section */
    .left_section {
        width: 80%;
    }


    /* Right section */
    .right_section {
        width: 70%;

    }

    .price_container h3 {
        font-size: 1.5vw;
        letter-spacing: .15rem;
    }


    /* Shadow */
    .shadow1 {
        right: 17vw;
    }

}


@media (max-width: 501px) {

    /* Shadow */
    .shadow2 {
        bottom: 0.6rem;
        right: .61rem;
    }

    .Hero_container {
        align-items: start;
        padding-top: 2rem;
    }
}

@media (max-width: 400px) {
    .Hero_container .hero_containt h1 {
        font-size: clamp(1rem, 15vw, 6rem);

    }
    .hero_containt h4 {
    font-size: clamp(1rem, 8vw, 7rem);

    }
}