.Cart_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
    margin-bottom: 10rem;
}

.cart_containt {
    width: 90%;
    /* border: 2px solid orangered; */
}

.cart_containt .title {
    text-align: center;
    color: brown;
    padding-bottom: 2rem;
}

.cart_items_container {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
}

.cart_items_containt {
    display: flex;
    flex-direction: column;
    width: 70%;
    /* border: 1px solid black; */
    gap: 1rem;
    min-height: 100vh;
}




/* Single Item */
.single_item {
    width: 100%;
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.171);
}

.cart_containt .single_item img {
    width: 6rem;
    height: 5rem;
    object-fit: cover
}

.cart_containt .single_item .details {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.cart_containt .single_item .details_containt {
    display: flex;
    gap: 2rem;
}

.cart_containt .single_item .name_price h5 {
    margin: 0;
    font-size: 1.135rem;
}

.cart_containt .single_item .name_price p {
    margin: 0;
    font-size: 1.2rem;
    font-weight: bold;
    color: brown;
}

.cart_containt .single_item .quantity_total {
    display: flex;
    gap: 2rem;
}

.Cart_container .single_item .each_price_1 {
    display: block;
}

.Cart_container .single_item .each_price {
    display: none;
}

.cart_containt .single_item .quantity_total input {
    height: 2.2rem;
    width: 4rem;
    font-size: 1.2rem;
    text-align: center;
}

.cart_containt .single_item .total p {
    margin: 0;
    color: rgba(0, 0, 0, 0.589);
}

.cart_containt .single_item  .total h5{
   margin: 0;
   margin-top: .0135rem;
   font-size: 1.2rem;
}

.cart_containt .single_item .delete {
    /* border: 1px solid red; */
    width: 10%;
}

/* .cart_containt .single_item .delete button {
    padding: .2rem 2rem;
    background-color: orange;
    color: brown;
    font-size: 1.1rem;
    border: none;
    cursor: pointer;
} */

.cart_containt .single_item .delete i {
    font-size: 1.7rem;
    color: brown;
    cursor: pointer;
    transition: .25s;
}

.cart_containt .single_item .delete i:hover {
    font-size: 2rem;
    color: red;
}




/* Total container */
.total_container {
    /* box-shadow: 0 0 1rem rgba(0, 0, 0, 0.171); */
    width: 30%;
    position: relative;
}

.cart_containt .total_containt {
    position: fixed;
   /* border: 1px solid red; */
   width: 26%;
   height: 30rem;
   display: flex;
   flex-direction: column;
   gap: 1rem;
   /* background-color: rgba(255, 166, 0, 0.76); */
}

/* Coupon */

.Cart_container .total_container .coupon {
    /* border: 1px solid green; */
    padding: 1rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.171);
    /* width: 100%; */
}

.cart_containt .coupon .apply {
    display: flex;
    width: 100%;

}

.cart_containt .coupon input {
    border: 1px solid orange;
    border-right: none;
    font-size: 1.1rem;
    padding: .3rem;
    width: 70%;
}

.cart_containt .coupon input:focus {
    outline: none;
}

.cart_containt .coupon button {
    border: 1px solid orange;
    background-color: orange;
    color: brown;
    width: 30%;
    cursor: pointer;
}


/* total_price */
.cart_containt .total_container .total_price {
   display: flex;
   flex-direction: column;
   box-shadow: 0 0 1rem rgba(0, 0, 0, 0.171);
   padding: 1rem;

}

.cart_containt .total_container .total_price_containt {
    display: flex;
    gap: 2rem;
}

.cart_containt .total_container .total_price_containt .tot p {
    font-size: 1.2rem;
}

.cart_containt  .total_price_containt .number  p {
    font-weight: bold;
    color: brown;
    font-size: 1.2rem;
}

.cart_containt .total_container .submit {
    width: 100%;
    margin-top: 1rem;
    /* border: 1px solid red; */
}

.cart_containt .total_container .submit button {
    width: 100%;
    padding: .4rem 0;
    font-size: 1rem;
    background-color: orange;
    color: brown;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: .25s;
}

.cart_containt .total_container .submit button:hover {
    border-radius: 10rem;
}



/* Media Query */
@media (max-width: 1050px) {
    .cart_containt .total_containt {
        position: relative;
        width: 100%;
        height: auto;
        flex-direction: row;
        /* border: 1px solid red; */


    }

    .cart_items_container {
        flex-direction: column;
    }

    .cart_items_containt {
        width: 100%;
    }

    .total_container {
        width: 100%;
    }

    .Cart_container .total_container .coupon {
        width: 40%;
    }

    .Cart_container .total_container .total_price {
        width: 60%;
    }


}


@media (max-width: 700px) {
    .Cart_container .single_item .details_containt {
        flex-direction: column;
        gap: .51rem;
    }

    .Cart_container .single_item .name_price p{
        display: none;
    }

    .Cart_container .single_item .each_price_1 {
        display: none;
    }

    .Cart_container .single_item .each_price {
        display: block;
    }

    .Cart_container .single_item .quantity_total h5 {
        color: brown;
    }

    .cart_containt .total_containt {
        flex-direction: column;
    }
    
    .Cart_container .total_container .coupon {
        width: 95%;
    }

    .Cart_container .total_container .total_price {
        width: 95%;
    }


}

@media (max-width: 501px) {
    .cart_containt .single_item img {
        width: 4rem;
    }

    .cart_containt .single_item .details {
        gap: .5rem;
    }

    .cart_containt .single_item .quantity_total input {
        width: 2.8rem;

    }

    .cart_containt .single_item .name_price h5 {
        white-space: nowrap;
    }

    .cart_containt .single_item {
        gap: 0;
    }

    .Cart_container {
        margin-bottom: 3rem;
    }
}

@media (max-width: 375px) {
    .cart_containt .single_item .quantity_total {
        gap: 1rem;

    }

    .cart_containt .single_item .delete {
        /* border: 1px solid red; */
    }

    .cart_containt .single_item .name_price h5 {
        white-space: normal;
        font-size: 1rem;
    }

    .cart_containt .single_item .quantity_total input {
        width: 2.5rem;
        height: 1.5rem;

    }

    .cart_containt .single_item .quantity_total .total h5 {
        font-size: 1rem;
    }

    .cart_containt .single_item .delete i {
        font-size: 1.5rem;
    }

    .cart_containt .single_item img {
        width: 3rem;
;
    }




}