.Burgers_container {
    margin-top: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    /* text-align: center; */
    width: 100vw;
    padding-bottom: 10rem;
}

.burgers_containt {
    width: 90%;
    /* border: 2px solid orangered; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.burgers_containt .burgers_list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, 18rem);
    justify-content: center;
    /* border: 2px solid green; */
    gap: 1.5rem;
}


/* title_section */
.title_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 3rem;
}


.title_section h2 {
    margin: 0;
}

.title_section p {
    margin: .5rem;
    font-size: 1.2rem;
    color: brown;
    font-weight: bold;
}

/* Single burger */


.burgers_containt .single_bugers {
    position: relative;
    /* border: 1px solid red; */
    box-shadow: 0 0 2rem rgb(175, 175, 175);
    box-shadow: 0 0 2rem rgb(175, 175, 175);
    box-shadow: 0 0 2rem rgba(0, 0, 0, 0.192);
    /* width: 20rem; */
    padding: 1rem;
    border-radius: 1rem;
}

.burgers_containt .single_bugers img {
    width: 100%;
    height: 15rem;
    object-fit: cover;
    border-radius: .6rem;
}

.burgers_containt .single_bugers .shadow_box {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.burgers_containt .single_bugers .shadow {
    /* border: 1px solid red; */
    width: 70%;
    height: 3rem;
    object-fit: cover;
    object-position: bottom;
    transform: translateY(-2.6rem);
    opacity: .7;
    /* padding: 0 3rem; */

}



/* Start */
.burgers_containt .stars_section {
    display: flex;
    gap: .25rem;
}


.burgers_containt .stars_section i {
     font-size: 1.3rem;
     color: brown;
}

.burgers_containt .single_bugers h3 {
    font-size: 1.4rem;
    font-size: 1.2rem;
    margin: 0;
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
}

.burgers_containt .single_bugers p {
    font-size: 1.1rem;
    color: rgba(0, 0, 0, 0.63);
    margin-top: .30rem;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2; 
    -webkit-box-orient: vertical;
  

}



.burgers_containt .single_bugers .button_price {
    display: flex;
    justify-content: start;
    gap: .50rem;
    width: 100%;
    /* border: 1px solid red; */

}

.burgers_containt .single_bugers .button_price button {
    /* width: 45%; */
    height: 2rem;
    background-color: orange;
    color: brown;
    border: none;
    cursor: pointer;
    transition: .35s;
    font-weight: bold;
    font-size: 1rem;
    padding: 0 1rem;
    border-radius: .3rem;
}

.burgers_containt .single_bugers .button_price button:hover {
    border-radius: 10rem;
}

.burgers_containt .single_bugers .button_price p {
    font-size: 1.6rem;
    margin: 0;
    font-weight: bold;
    color: brown;
}


/* favorit_heart */
.burgers_containt .single_bugers .favorit_heart  {
    position: absolute;
    /* border: 1px solid brown; */
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10rem;
    font-size: 1.1rem;
    color: brown;
    right: 1.52rem;
    top: 1.52rem;
    cursor: pointer;
    background-color: white;
}

@media(max-width: 600px) {
    .burgers_containt .burgers_list {
        grid-template-columns: repeat(auto-fill, 100%);
        gap: 1rem;
    }

    .burgers_containt .single_bugers img {
        height: 15rem;
    }

    .burgers_containt .single_bugers .stars_section i {
        font-size: 1rem;
    }

    .burgers_containt .single_bugers h3 {
        font-size: 1.1rem;
    }

    .burgers_containt .single_bugers p {
        font-size: 1rem;
    }

    .burgers_containt .single_bugers .button_price button {
        font-size: .81rem;
        height: auto ;
        padding: 0rem .6rem;
    }

    .burgers_containt .single_bugers .button_price p {
        font-size: 1.3rem;
    }
}

@media (max-width: 400px) {
    .burgers_containt .single_bugers img {
        height: 10rem;
    }
}

@media (max-width: 300px) {
    .burgers_containt .burgers_list {
        grid-template-columns: repeat(auto-fill, 90%);
        gap: 1rem;
    }

    .burgers_containt .title_section h2 {
        font-size: 1rem;
    }
}