.Footer_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;

}

.footer_containt {
    position: relative;
    width: 100%;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.973), rgba(0, 0, 0, 0.479)), url("./Image/footer_img_1.jpg");
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
    

}



.footer_containt .footer_details_containt {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 3rem;
    margin-bottom: 10rem;

}

.footer_containt  .footer_section_1 {
    width: 50%;
}

.footer_containt .footer_section .logo {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}


.footer_containt .footer_section .logo img {
    width: 6rem;
} 

@font-face {
    font-family: footer_font;
    src: url(../../Font-familly/Flamenco-Regular.ttf);
}

.footer_containt .footer_section .logo h3 {
    font-size: 3.1rem;
    font-family: footer_font;
    color: rgba(255, 255, 255, 0.753);
}

.footer_containt .footer_section .description {
    font-size: 1.2rem;
    color: rgba(255, 255, 255, 0.788);
    line-height: 2rem;
}

/* footer_section_2 */
.footer_containt .footer_section_2 {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 35%;
    padding-top: 6rem;
    
}

.footer_containt .footer_section_2 .personal_info {
    display: flex;
    align-items: center;
    gap: 2rem;
}



.footer_containt .footer_section_2 i {
    font-size: 2rem;
}

.footer_containt .footer_section_2 p {
    font-size: 1.3452rem;
    margin: 0;
    color: rgb(255, 255, 255);
}


/* copywrite_container */
.footer_containt .copywrite_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 3rem;
    position: absolute;
    bottom: 2rem;
    left: 2rem;
    right: 2rem;


}

.footer_containt .copywrite_container h5 {
    font-size: .9rem;
    text-transform: uppercase;
    color: rgba(197, 197, 197, 0.795);
    margin: 0;
}

.footer_containt .copywrite_container .right {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.footer_containt .copywrite_container .right i {
    font-size: 1.5rem;
    cursor: pointer;
    transition: .25s;
}

.footer_containt .copywrite_container .right i:hover {
    color: orangered;

}


/* Media Query */
@media (max-width: 1000px) {
    .footer_containt .footer_details_containt {
        flex-direction: column;
    }

    .footer_containt .footer_section {
        width: 100%;
    }

    .footer_containt .footer_section_2 {
        padding-top: 0;
    }
}


@media (max-width: 800px) {
    .footer_containt .footer_section .logo h3 {
        font-size: 2.2rem;
    }

    
    .footer_containt .footer_section .logo img {
    width: 3rem;
    } 


    .footer_containt .copywrite_container {
        flex-direction: column-reverse;
        align-items: start;
        gap: 1rem;
        left: 0rem;

    
    }



}


@media (max-width: 501px ){
    .footer_containt .footer_details_containt {
        padding: 1rem;
    
    }
}

