.About_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 9rem;
    padding-bottom: 6rem;
    min-height: 70vh;
}

.About_container .about_containt {
    display: flex;
    justify-content: center;
    gap: 1rem;
    width: 90%;
}

/* description */
.About_container .about_containt .description {
    width: 50%;
}

@font-face {
    font-family: ultra-regular;
    src: url(../../Font-familly/Ultra-Regular.ttf);
}

.About_container .about_containt .description h5 {
    margin: 0;
    font-size: 1.5rem;
    color: rgba(61, 38, 38, 0.815);
}

.About_container .about_containt .description h3 {
    font-size: 2.5rem;
    margin: 0;
    color: rgb(54, 23, 23);
    font-family: ultra-regular;
}

.About_container .about_containt .description p {
    font-size: 1.4rem;
    line-height: 2.2rem;
    color: rgba(0, 0, 0, 0.788);
    width: 90%;
}



/* Image section */
.About_container .about_containt .image_box {
    width: 50%;
}


.About_container .about_containt .image_box img {
    width: 100%;
}



/* Media Query */
@media (max-width: 1000px) {
    .About_container .about_containt {
        flex-direction: column-reverse;
    }

    .About_container .about_containt .description {
        width: 100%;
    }

    .About_container .about_containt .image_box {
        width: 100%;
    }
    
}

@media (max-width: 700px) {
    .about_containt .description h5{
       font-size: 1.5rem !important;
       font-size: clamp(.6rem, 5vw, 7rem) !important; 

    }

    .about_containt .description h3 {
        font-size: clamp(1rem, 5vw, 7rem) !important; 
        font-size: clamp(1rem, 6vw, 7rem) !important; 
    }

    .about_containt .description p {
        font-size:  1.2rem !important;
        font-size: clamp(1rem, 4vw, 2rem) !important;
        line-height: normal;
    }
}

@media (max-width: 500px) {
    .about_containt .description p {
        line-height: 1.8rem !important;
    }
}