.Contact_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10rem;
    margin-top: 2rem;
    overflow: hidden;
}

.Contact_container .contact_containt {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
    padding-bottom: 10rem;
    padding-top: 2rem;
    background-color: rgba(128, 128, 128, 0.116);

}


/* title_section */
.Contact_container .contact_containt  .title_section {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 99;
}

.Contact_container .contact_containt .title_section h5 {
    font-size: 1.8rem;
    color: rgb(48, 21, 21);
    margin: 0;
}

@font-face {
    font-family: moul-regular;
    src: url(../../Font-familly/AlfaSlabOne-Regular.ttf);
}

.Contact_container .contact_containt .title_section h2 {
    font-size: 2.8rem;
    margin: .5rem;
    color: rgb(83, 29, 29);
    font-family: moul-regular;
    text-align: center;
}


/* form */
.Contact_container .contact_containt form {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-top: 5rem;
    width: 70%;
    min-width: 50rem;
    
}

.Contact_container .contact_containt form .form_section {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}


.Contact_container .contact_containt form input {
    height: 4rem;
    border-radius: .25rem;
    border: 1px solid gray;
    padding-left: 1.5rem;
    font-size: 1.2rem;
    z-index: 99;
}

.Contact_container .contact_containt form input:focus {
    outline: none;
}

.Contact_container .contact_containt form input::placeholder {
    color: rgba(37, 37, 37, 0.932);
    font-weight: 300;
    font-size: 1rem;
}

.Contact_container .contact_containt form button {
    height: 4rem;
    font-size: 1.5rem;
    font-family: moul-regular;
    color: brown;
    background-color: orange;
    border: none;
    z-index: 99;
    cursor: pointer;
}






/* contact_img */
.Contact_container .contact_containt .contact_img {
    position: absolute;
    width: 15rem;
}

.Contact_container .contact_containt .Ketchup_img {
    left: 1rem;
    width: 14rem;
    bottom: 9rem;
}

.Contact_container .contact_containt .burger_img {
    top: -0rem;
    left: -0rem;
    width: 10rem;
}

.Contact_container .contact_containt .plate_img {
    right: -3rem;
    bottom: -2rem;
    width: 22rem;
}



/* Media Query */
@media (max-width: 950px) {
    .Contact_container .contact_containt form {
        width: 90%;
        min-width: 0rem;
        
    }
}

@media (max-width: 750px) {
.Contact_container .contact_containt form {
     flex-direction: column;
}

.Contact_container .contact_containt form .form_section {
     width: 100%;
}
}


@media (max-width: 600px) {
    .contact_containt .title_section h5 {
        font-size: 1.4rem !important;
    }

    .contact_containt .title_section h2 {
        font-size: 2.4rem !important;
    }
}


@media (max-width: 501px) {
    .Contact_container .contact_containt .burger_img {
        top: 11rem;
        left: -0rem;
        width: 7rem;
    }

    .Contact_container .contact_containt .Ketchup_img {
        left: 12rem;
        width: 20rem;
        bottom: 25rem;
    }
}

