.Galerie_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 6rem;
    margin-bottom: 6rem;
}

.Galerie_container .galerie_containt {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    gap: 2rem;
}


/* Left section */
.Galerie_container .left_section {
    position: relative;
    width: 50%;
}

.Galerie_container .left_section img {
    object-fit: cover;
    width: 100%;
    height: 25rem;
    border-radius: 1rem;

}

.Galerie_container .left_section .title {
    position: absolute;
    left: 2rem;
    top: 2rem;
    color: white;
}

.Galerie_container .left_section h5 {
    font-size: 1rem;
    margin: 0;
}

.Galerie_container .left_section h3 {
    font-size: 2rem;
    margin: 0;
}




/* right section */
.Galerie_container .right_section {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 50%;
}

.Galerie_container .right_section img {
    object-fit: cover;
    object-position: bottom left;
    width: 100%;
    height: 11.5rem;
    border-radius: 1rem;
}

.Galerie_container .right_section .image_2,
.Galerie_container .right_section .image_3 {
    position: relative;
}

.Galerie_container .right_section .image_3 img {
    transform: rotateY(180deg);
}

.Galerie_container .right_section .title {
    position: absolute;
    top: 1rem;
    left: 2rem;
    color: white;
}

.Galerie_container .right_section h5 {
    margin: 0;
    font-size: 1.2rem;
    color: rgb(177, 157, 157);
}

.Galerie_container .right_section h3 {
    margin: 0;
    font-size: 1.5rem;
}


/* galerie_btn */
.Galerie_container .galerie_btn {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
}

.Galerie_container .galerie_btn button {
    cursor: pointer;
    border: none;
    width: 15rem;
    height: 2.5rem;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(88, 40, 40);
    background-color: orange;
    transition: .25s;
    border-radius: .4rem;
}

.Galerie_container .galerie_btn button:hover {
    width: 17rem;
    border-radius: 1rem;
}



/* Media Query */
@media (max-width: 700px) {
    .Galerie_container .galerie_containt {
        flex-direction: column;
    }

    .Galerie_container .left_section {
        width: 90%;
    }

    .Galerie_container .right_section {
        width: 90%;
    }

    
}

@media (max-width: 501px) {
    .galerie_containt .title h5 {
        font-size: .81rem;
    }

    .galerie_containt .title h3 {
        font-size: 1.1rem;
    }

    .galerie_containt .right_section .title,
    .galerie_containt .left_section .title {
        top: 1rem;
        left: 1rem;
    }

    .galerie_containt .right_section .image_2 img {
        object-position:  left !important;
    }

    .galerie_containt .right_section .image_3 img {
        object-position:  center !important;
    }
}